.login {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: center/cover url('~@/assets/login.png');

  .login-container {
    @media all and (max-width: 767px) {
      width: 90%;
      height: 420px;
    }
    width: 400px;
    height: 420px;
    background-color: rgb(231, 227, 227);
    position: absolute;
    left: 50%;
    top: 45%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 50px rgb(0 0 0 / 10%);
    opacity: 0.8;
  }

  .login-logo {
    width: 200px;
    height: 180px;
    display: block;
    margin: 0 auto 20px;
  }

  .login-checkbox-label {
    font-size: 16px;
    font-weight: bold;
    color: rgb(196, 202, 38);
  }

  .login-form-font {
    font-size: 15px;
    font-weight: bold;
    color: rgb(60, 60, 60);
  }

  .login-form-forgot {
    font-size: 16px;
    font-weight: bold;
    float: right;
    color: red;
  }

  .login-form-register {
    font-size: 17px;
    font-weight: bold;
    color: green;
  }

  .dash {
    display: flex;
    /* 垂直置中 */
    align-content: center;
  }

  .content {
    font-size: 16px;
    font-weight: bold;
    color: rgb(227, 8, 34);
    display: flex;
    /* 水平置中 */
    justify-content: center;
    /* 垂直置中 */
    align-content: center;
  }

  .setInfo {
    width: 100%;
    position: absolute;
    top: 85%;
  }
}
